import React, { useState, useEffect, forwardRef } from "react";
import Prism from "prismjs";
import { EditorField } from "../../types/Form";
import "./CodeEditor.scss";

export interface CodeEditorProps {
  data: EditorField;
  editorLanguage: "css" | "javascript";
  onChange?: (content?: string) => void;
  value?: string;
}

const CodeEditor = forwardRef<HTMLTextAreaElement, CodeEditorProps>(
  ({ value, editorLanguage = "css", onChange }, ref) => {
    const [content, setContent] = useState(value);

    useEffect(() => {
      Prism.highlightAll();
    }, []);

    useEffect(() => {
      setContent(value);
    }, [value]);

    useEffect(() => {
      Prism.highlightAll();
      onChange && onChange(content);
    }, [editorLanguage, content, onChange]);

    return (
      <div className="code-edit-container">
        <textarea
          ref={ref}
          className="code-input"
          value={content}
          onChange={(evt) => setContent(evt.target.value)}
        />
        <pre className="code-output">
          <code className={`lang-${editorLanguage}`}>{content}</code>
        </pre>
      </div>
    );
  }
);

export default CodeEditor;
