import { FieldItem } from "../../types/Form";

export default function getValueObjFromData(data: Record<string, FieldItem[]>) {
  const defaultValues: Record<string, any> = Object.keys(data).reduce(
    (obj, item) => {
      return {
        ...obj,
        [item]: data[item].reduce(
          (obj2, i2) => ({
            ...obj2,
            [i2.name]: i2.value,
          }),
          {}
        ),
      };
    },
    {}
  );
  return defaultValues;
}
