import React, { Component, CSSProperties } from "react";
import { spring, TransitionMotion, presets } from "react-motion";
import Modal from "../../components/Modal/Modal";
import MyForm from "../../components/MyForm/MyForm";
import { FieldItem } from "../../types/Form";

export interface Props {
  generalDefaultValue: Record<string, Object>;
  onUpdateStore: (store: Record<string, any>, noPostAjax?: boolean) => void;
  defaultValue: Record<string, any>;
  itemFields: FieldItem[];
  isLoading: boolean;
}

export interface State {
  isOpenModal: boolean;
  itemActive: string;
  recentAddnew: string;
}

const WilokeNotificationBarTranslations =
  window.WilokeNBGlob.WilokeNotificationBarTranslations;

export default class TabSliderSetting extends Component<Props, State> {
  static defaultProps = {
    defaultValue: {},
    onUpdateStore: () => {},
  };

  state: Readonly<State> = {
    isOpenModal: false,
    itemActive: "",
    recentAddnew: "",
  };

  _timeOut: NodeJS.Timeout | null = null;
  _timeOutExitAlert: NodeJS.Timeout | null = null;

  componentDidUpdate(prevProps: Props, prevState: State) {
    const { defaultValue } = this.props;
    const { recentAddnew, itemActive } = this.state;
    if (
      JSON.stringify(prevProps.defaultValue) !== JSON.stringify(defaultValue)
    ) {
      if (
        Object.keys(defaultValue).includes(recentAddnew) &&
        recentAddnew === itemActive
      ) {
        this.setState({ isOpenModal: true });
      }
    }
  }

  getBannerName = (key: string) => {
    const item = this.props.defaultValue[key];
    if (!item) return "";
    return item.bannerName;
  };

  handleClickEdit = async (value: string) => {
    await this.setState({ itemActive: value });
    this.setState({ isOpenModal: true });
  };

  _deleteKeyFromDefaultValue = (
    key: string,
    defaultValue: Record<string, any>
  ) => {
    return Object.keys(defaultValue).reduce((obj, item) => {
      if (item === key) return obj;
      return {
        ...obj,
        [item]: defaultValue[item],
      };
    }, {});
  };

  hanldeDeleteItem = async (value: string) => {
    const { defaultValue, onUpdateStore } = this.props;

    const deleteText = `${
      WilokeNotificationBarTranslations.deleteTheBanner
    } "${this.getBannerName(value)}?"`;
    // eslint-disable-next-line no-restricted-globals
    if (confirm(deleteText)) {
      const newStore = this._deleteKeyFromDefaultValue(value, defaultValue);
      onUpdateStore(newStore);
    }
  };

  handleClickAddNew = async () => {
    const { defaultValue, generalDefaultValue, onUpdateStore } = this.props;

    const newKey = String(Date.now());
    const newStore = { ...defaultValue, [newKey]: generalDefaultValue };
    onUpdateStore(newStore, true);

    this.setState({ recentAddnew: newKey, itemActive: newKey });
  };

  handleSubmitForm = async (value: any) => {
    const { itemActive, recentAddnew } = this.state;
    const { defaultValue, onUpdateStore } = this.props;
    const newStore = {
      ...defaultValue,
      [itemActive]: { ...(defaultValue[itemActive] || {}), ...value },
    };
    onUpdateStore(newStore);
    this.setState({ isOpenModal: false });
    if (recentAddnew === value) {
      this.setState({ recentAddnew: "" });
    }
  };

  renderHeaderGroup = (value: string, index: number, style: CSSProperties) => {
    return (
      <div
        className="w-100 ba b--black-20 br2 relative hide-child bg-white flex items-center z-1 truncate"
        key={value + index}
        style={style}
      >
        <span className="truncate pa3"> {this.getBannerName(value)}</span>
        <div
          className="absolute child right-1"
          style={{
            transform: "translateY(-50%)",
            top: "50%",
          }}
        >
          <button
            className="pv2 ph3 white bg-blue bw0 br2 pointer mr2 w3"
            onClick={() => this.handleClickEdit(value)}
          >
            {WilokeNotificationBarTranslations?.edit || "Edit"}
          </button>
          <button
            type="button"
            className="pv2 ph3 white bg-red bw0 br2 pointer"
            onClick={() => this.hanldeDeleteItem(value)}
          >
            {WilokeNotificationBarTranslations?.delete || "Delete"}
          </button>
        </div>
      </div>
    );
  };

  handleCloseModal = async () => {
    const { defaultValue, onUpdateStore } = this.props;
    const { recentAddnew, itemActive } = this.state;
    if (recentAddnew === itemActive) {
      const newStore = this._deleteKeyFromDefaultValue(
        itemActive,
        defaultValue
      );
      onUpdateStore(newStore, true);
      this.setState({
        recentAddnew: "",
      });
    }

    this.setState({ isOpenModal: false });
  };

  _renderSubmitForm = (loading: boolean) => {
    return (
      <div className="fixed bottom-0 left-0 right-0 pa3 bg-white z-999">
        <div className="container">
          <button
            type="submit"
            className="pv3 ph4 white bg-pink bw0 br2 pointer w-100 center flex items-center justify-center fw6 tracked-mega ttu"
            disabled={loading}
          >
            {loading ? (
              <div className="loader" />
            ) : (
              window.WilokeNBGlob?.WilokeNotificationBarTranslations?.submit ||
              "S U B M I T"
            )}
          </button>
        </div>
      </div>
    );
  };

  renderModal = () => {
    const { defaultValue, itemFields, isLoading } = this.props;
    const { isOpenModal, itemActive } = this.state;

    const unmount = !itemActive || !isOpenModal;

    return (
      <TransitionMotion
        willEnter={() => ({ left: -window.innerWidth + 300, opacity: 1 })}
        willLeave={() => ({
          left: spring(window.innerWidth),
          opacity: spring(0),
        })}
        styles={
          unmount
            ? []
            : [
                {
                  key: itemActive,
                  style: {
                    left: spring(0, presets.stiff),
                    opacity: spring(1, presets.stiff),
                  },
                  data: defaultValue,
                },
              ]
        }
      >
        {(interpolatedStyles) => (
          <div>
            {interpolatedStyles.map((config) => {
              const defaultValue = config.data[config.key];
              return (
                <Modal
                  onClose={this.handleCloseModal}
                  key={config.key}
                  styles={{
                    transform: !!config.style.left
                      ? `translateX(${config.style.left}px)`
                      : "",
                    opacity: config.style.opacity,
                  }}
                >
                  {config.key && (
                    <div className="container">
                      <div className="mb5 pv4 b--black-20 br3">
                        <h2 className="mt0">
                          {this.getBannerName(itemActive)}
                        </h2>
                        <MyForm
                          data={itemFields}
                          onSubmit={this.handleSubmitForm}
                          isLoading={isLoading}
                          defaultValues={defaultValue}
                          renderSubmit={this._renderSubmitForm}
                        />
                      </div>
                    </div>
                  )}
                </Modal>
              );
            })}
          </div>
        )}
      </TransitionMotion>
    );
  };

  render() {
    const { defaultValue } = this.props;
    return (
      <div>
        <TransitionMotion
          willEnter={() => ({ marginB: 0, height: 0, opacity: 1, bw: 0 })}
          willLeave={() => ({
            bw: spring(0),
            marginB: spring(0),
            opacity: spring(0),
            height: spring(0),
          })}
          styles={Object.keys(defaultValue).map((item, index) => ({
            key: item,
            style: {
              marginB: spring(10, presets.noWobble),
              height: spring(55, presets.noWobble),
              opacity: spring(1, presets.noWobble),
              bw: spring(1, presets.noWobble),
            },
            data: index,
          }))}
        >
          {(interpolatedStyles) => (
            <div>
              {interpolatedStyles.map((config) => {
                return this.renderHeaderGroup(config.key, config.data, {
                  height: `${config.style.height}px`,
                  marginBottom: `${config.style.marginB}px`,
                  opacity: `${config.style.opacity}`,
                  borderWidth: `${config.style.bw}px`,
                });
              })}
            </div>
          )}
        </TransitionMotion>

        <button
          type="button"
          className="pv3 ph4 black fw6 bg-white ba b--pink bw1 br2 pointer w-100 tracked ttu f6 z-2 relative"
          onClick={this.handleClickAddNew}
        >
          + {WilokeNotificationBarTranslations?.addNew || "Add new"}
        </button>
        {this.renderModal()}
      </div>
    );
  }
}
