import React, { useState, useEffect } from "react";
import DatePicker, { ReactDatePickerProps } from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { DatePickerField } from "../../types/Form";

export interface MyDatePickerProps {
  data: DatePickerField;
  onChange?: (date: number | null) => void;
  value?: number;
}

const ExampleCustomInput = React.forwardRef<any, any>(
  ({ value, onClick }, ref) => (
    <button
      className="pv3 ba ph4 lh-solid bg-white br2 b--pink black pointer"
      onClick={onClick}
      type="button"
      ref={ref as any}
      style={{ minWidth: 200 }}
    >
      {value}
    </button>
  )
);

const MyDatePicker = React.forwardRef<ReactDatePickerProps, MyDatePickerProps>(
  ({ value, onChange }, ref) => {
    const [startDate, setStartDate] = useState<Date | null>(
      !!value ? new Date(Number(value)) : null
    );

    useEffect(() => {
      setStartDate(!!value ? new Date(Number(value)) : null);
    }, [value]);

    useEffect(() => {
      const dateNum = !!startDate ? new Date(startDate).getTime() : startDate;
      onChange && onChange(dateNum);
    }, [onChange, startDate]);

    return (
      <div className="relative dib hide-child">
        <DatePicker
          selected={startDate as Date}
          onChange={(date) => {
            if (Array.isArray(date) || !date) return;
            setStartDate(date);
          }}
          ref={ref as any}
          customInput={<ExampleCustomInput />}
        />
        <div
          className="absolute bg-pink white f6 lh-solid pointer pa1 br2 flex items-center justify-center tc child"
          style={{
            left: "calc(100% + 2px)",
            bottom: "calc(100% - 20px)",
            width: 20,
            height: 20,
          }}
          title="clear"
          onClick={() => setStartDate(null)}
        >
          &times;
        </div>
      </div>
    );
  }
);

export default MyDatePicker;
