import React, { useState, useEffect } from "react";
import { Motion, spring } from "react-motion";
import { Tab } from "./HomePage";
import styles from "./HomePage.module.scss";

export interface TabHeaderProps {
  tabs: Tab[];
  defaultActive: string;
  onChange: (tab: Tab) => void;
}

export default function TabHeader({
  tabs,
  defaultActive,
  onChange,
}: TabHeaderProps) {
  const [tabActive, setTabActive] = useState(defaultActive);

  useEffect(() => {
    setTabActive(defaultActive);
  }, [defaultActive]);

  useEffect(() => {
    setTabActive(tabActive);
  }, [tabActive]);

  const handleClickTab = (value: Tab) => {
    if (tabActive === value.name) return;
    setTabActive(value.name);
    onChange(value);
  };

  const renderTab = (value: Tab) => {
    const active = tabActive === value.name;

    return (
      <Motion
        key={value.name}
        defaultStyle={{ scale: 0.95 }}
        style={{
          scale: spring(active ? 1 : 0.95, {
            stiffness: 300,
            damping: 10,
          }),
        }}
      >
        {(interpolatingStyle) => {
          const CLASS = active ? styles.tabItemActive : "";
          return (
            <div
              className={`${styles.tabItem} ${CLASS}`}
              style={{ transform: `scale(${interpolatingStyle.scale})` }}
              onClick={() => handleClickTab(value)}
              key={value.name}
            >
              <span className="lh-solid">{value.label}</span>
            </div>
          );
        }}
      </Motion>
    );
  };

  return <div className={styles.tabs}>{tabs.map(renderTab)}</div>;
}
