import ReactDOM from "react-dom";
import React, { CSSProperties, ReactNode } from "react";

export interface ModalProps {
  children: ReactNode;
  onClose?: () => void;
  styles?: CSSProperties;
}

export default class Modal extends React.Component<ModalProps, {}> {
  static defaultProps: {
    onClose: () => {};
    styles: {};
  };

  modalRoot = document.querySelector("#wilModals");
  el = document.createElement("div");
  componentDidMount() {
    this.modalRoot && this.modalRoot.appendChild(this.el);
  }

  componentWillUnmount() {
    this.modalRoot && this.modalRoot.removeChild(this.el);
  }

  render() {
    return ReactDOM.createPortal(
      <div
        className="fixed absolute--fill bg-white z-9999 overflow-auto"
        style={this.props.styles}
      >
        <span
          className="red fixed top-1 right-1 pa2 bg-white pointer lh-solid f2 z-9999"
          onClick={this.props.onClose}
        >
          &times;
        </span>
        {this.props.children}
      </div>,
      this.el
    );
  }
}
