import React, { useState, useEffect } from "react";
import { InputFileField as MyInputFile } from "../../types/Form";
declare global {
  var wp: any;
}

type InputFileProps = React.DetailedHTMLProps<
  React.InputHTMLAttributes<HTMLInputElement>,
  HTMLInputElement
> & {
  data?: MyInputFile;
  onChange?: (url: string) => void;
  value?: string;
};

const InputFile = React.forwardRef<HTMLDivElement, InputFileProps>(
  ({ onChange, value }, ref) => {
    const [imageUrl, setImageUrl] = useState(value);

    useEffect(() => {
      setImageUrl(value);
    }, [value]);

    useEffect(() => {
      onChange && onChange(imageUrl || "");
    }, [imageUrl, onChange]);

    const handleClickBtnUpload = (
      event: React.MouseEvent<HTMLDivElement, MouseEvent>
    ) => {
      event && event.preventDefault();
      let frame: any;
      // If the media frame already exists, reopen it.
      if (frame) {
        frame.open();
        return;
      }

      // Create a new media frame
      frame = wp.media({
        title: "Select or Upload Media Of Your Chosen Persuasion",
        button: {
          text: "Use this media",
        },
        multiple: false,
      });

      frame.on("select", function () {
        var attachment = frame.state().get("selection").first().toJSON();
        console.log(11, { attachment: attachment });
        setImageUrl(attachment.url);
      });
      // Finally, open the modal on click
      frame.open();
    };

    const handleClickDeleteImage = () => setImageUrl("");

    const renderImage = () => {
      return (
        <article className="mw4">
          <div className="aspect-ratio aspect-ratio--4x3 mb2 relative ba b--black-10">
            <div
              className="aspect-ratio--object cover br2"
              title={imageUrl}
              style={{
                background: `url(${imageUrl}) center`,
                zIndex: 1,
              }}
            ></div>
            <span
              className="absolute top--1 right--1 red ba w1 h1 z-1 f6 lh-solid bg-white flex items-center justify-center br-pill pointer b"
              onClick={handleClickDeleteImage}
            >
              &times;
            </span>
          </div>
        </article>
      );
    };

    const renderButtonUpload = () => {
      return (
        <div
          className="ba b--black-20 pa2 pointer br2 tc"
          onClick={handleClickBtnUpload}
        >
          {window.WilokeNBGlob?.WilokeNotificationBarTranslations?.upload ||
            "Upload"}
        </div>
      );
    };

    return (
      <div className="pa3 ba center b--black-30 b--dashed br2" ref={ref}>
        {!!imageUrl && renderImage()}
        {renderButtonUpload()}
      </div>
    );
  }
);

export default InputFile;
