import React, { FC, useState, useEffect } from "react";
import { SwitchField } from "../../types/Form";
import styles from "./Switch.module.scss";

export interface SwitchProps {
  onChange?: (checked: boolean) => {};
  data: SwitchField;
  value?: boolean;
}

const Switch: FC<SwitchProps> = React.forwardRef<HTMLDivElement, SwitchProps>(
  ({ data, onChange, value }, ref) => {
    const [isChecked, setIsChecked] = useState(value);

    useEffect(() => {
      setIsChecked(value);
    }, [value]);

    const CLASS = isChecked ? "ant-switch-checked" : "";
    return (
      <div className={styles.container} ref={ref}>
        <button
          type="button"
          role="switch"
          aria-checked="true"
          className={`ant-switch ${CLASS}`}
          onClick={(e) => {
            e && e.preventDefault();
            setIsChecked(!isChecked);
            onChange && onChange(!isChecked);
          }}
        >
          <div className="ant-switch-handle"></div>
          <span className="ant-switch-inner"></span>
        </button>
      </div>
    );
  }
);

export default Switch;
