import React, { Component } from "react";
import Axios from "axios";
import MyForm from "../../components/MyForm/MyForm";
import { FieldItem } from "../../types/Form";
import { Motion, spring } from "react-motion";
import TabSliderSetting from "./TabSliderSetting";
import styles from "./HomePage.module.scss";
import getValueObjFromData from "./getValueObjFromData";
import { toast } from "react-toastify";
import TabHeader from "./TabHeader";

export interface Tab {
  name: string;
  label: string;
  fields: FieldItem[];
  data: Record<string, FieldItem[]>;
}
declare global {
  var wp: any;
  var ajaxurl: string;
  var WilokeNBGlob: {
    WilokeNotificationBarTabs: Tab[];
    WilokeNotificationBarTranslations: {
      heading: string;
      addNew: string;
      edit: string;
      delete: string;
      submit: string;
      upload: string;
      deleteTheBanner: string;
    };
  };
}

export interface Props {}
export interface State {
  tabActive: string;
  tabs: Tab[];
  store: Record<string, any>;
  isLoading: boolean;
}

const sliderItemfileds = window.WilokeNBGlob.WilokeNotificationBarTabs.filter(
  (item) => item.name === "slideItemSettings"
)[0].fields;

export default class HomePage extends Component<Props, State> {
  state: Readonly<State> = {
    tabs: window.WilokeNBGlob.WilokeNotificationBarTabs,
    tabActive: "",
    store: {},
    isLoading: false,
  };

  async componentDidMount() {
    const { tabs } = this.state;
    this.setState((state) => ({ tabActive: state.tabs[0].name }));
    const store = tabs.reduce((obj, item) => {
      if (item.name === "slideItemSettings") {
        return {
          ...obj,
          [item.name]: getValueObjFromData(item.data || {}),
        };
      }
      return {
        ...obj,
        ...getValueObjFromData({ [item.name]: item.fields }),
      };
    }, {});
    this.setState({ store });
  }

  handleAxiosAjax = async () => {
    const { store } = this.state;
    console.log(11, { store });

    const bodyFormData = new FormData();
    bodyFormData.append("data", JSON.stringify(store));
    bodyFormData.append("action", "wiloke_save_notification_bar");
    // bodyFormData.append("action", "wiloke_save_notification_bar_common");

    const config = {
      headers: {
        "Content-Type": "application/x-www-form-urlencoded",
      },
    };
    await Axios.post(window.ajaxurl, bodyFormData, config);
  };

  handlePostDataToServer = async () => {
    try {
      await this.handleAxiosAjax();
      toast.success("Data updated successfully!");
    } catch (error) {
      let errStr = "";
      if (error.response) {
        errStr = JSON.stringify(error.response.data);
      } else if (error.request) {
        errStr = JSON.stringify(error.request);
      } else {
        errStr = JSON.stringify(error.message);
      }
      toast.error(errStr);
    }
  };

  handleSubmitOtherTab = async (data: any) => {
    await this.setState((state) => ({
      isLoading: true,
      store: {
        ...state.store,
        [state.tabActive]: {
          ...(state.store[state.tabActive] || []),
          ...data,
        },
      },
    }));
    await this.handlePostDataToServer();
    this.setState({ isLoading: false });
  };

  handleUpdateStoreSlideItemSettings = async (
    data: Record<string, any>,
    noPostAjax?: boolean
  ) => {
    await this.setState({
      isLoading: true,
      store: {
        ...this.state.store,
        slideItemSettings: data,
      },
    });

    !noPostAjax && (await this.handlePostDataToServer());
    this.setState({ isLoading: false });
  };

  renderTabBody = () => {
    const { tabActive, tabs, isLoading, store } = this.state;
    if (!tabActive) return null;
    const data = tabs.filter((item) => item.name === tabActive)?.[0] || [];

    const defaultSlideValue = getValueObjFromData({ value: sliderItemfileds });

    if (tabActive === "slideItemSettings") {
      return (
        <TabSliderSetting
          itemFields={sliderItemfileds}
          isLoading={isLoading}
          onUpdateStore={this.handleUpdateStoreSlideItemSettings}
          generalDefaultValue={{
            ...defaultSlideValue.value,
            ...store["generalSettings"],
          }}
          defaultValue={store[tabActive]}
        />
      );
    }
    return (
      <MyForm
        data={data.fields}
        onSubmit={this.handleSubmitOtherTab}
        defaultValues={store[tabActive]}
        isLoading={isLoading}
      />
    );
  };

  render() {
    const { tabs, tabActive, isLoading } = this.state;
    return (
      <>
        <div className={styles.container}>
          <TabHeader
            tabs={tabs}
            defaultActive={tabActive}
            onChange={(value) => this.setState({ tabActive: value.name })}
          />
          <div className="bg-white pa4">{this.renderTabBody()}</div>
        </div>

        {isLoading && (
          <Motion
            defaultStyle={{ x: 0 }}
            style={{
              x: spring(window.innerWidth, { damping: 110, stiffness: 310 }),
            }}
          >
            {(value) => (
              <div
                className="fixed top-0 left-0 z-max"
                style={{ width: value.x, height: 4, backgroundColor: "red" }}
              />
            )}
          </Motion>
        )}
      </>
    );
  }
}
