import React, { FC, ReactNode, useEffect } from "react";
import { FieldItem } from "../../types/Form";
import { Controller, useForm } from "react-hook-form";
import Input from "../Input/Input";
import Textarea from "../Textarea/Textarea";
import ColorPicker from "../ColorPicker/ColorPicker";
import MyDatePicker from "../DatePicker/DatePicker";
import Select from "../Select/Select";
import InputFile from "../InputFile/InputFile";
import Switch from "../Switch/Switch";
import CodeEditor from "../CodeEditor/CodeEditor";

export interface MyFormProps {
  data: FieldItem[];
  onSubmit?: (data: any) => void;
  isLoading?: boolean;
  defaultValues: Record<string, any>;
  renderSubmit?: (loading: boolean) => ReactNode;
}

const MyForm: FC<MyFormProps> = ({
  data,
  onSubmit,
  isLoading = false,
  defaultValues,
  renderSubmit,
}) => {
  const { register, handleSubmit, errors, control, watch, reset } = useForm({
    defaultValues,
  });
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const { xxxxx } = watch();

  const _onSubmit = (data: any) => {
    onSubmit && onSubmit(data);
  };

  useEffect(() => {
    reset(defaultValues);
  }, [defaultValues, reset]);

  const renderItem = (value: FieldItem) => {
    switch (value.type) {
      case "text":
        return (
          <Input data={value} register={register} required={value.require} />
        );
      case "textarea":
        return (
          <Textarea data={value} register={register} required={value.require} />
        );
      case "select":
        return (
          <Controller
            as={Select}
            name={value.name}
            control={control}
            data={value}
          />
        );
      case "datePicker":
        return (
          <Controller
            as={MyDatePicker}
            name={value.name}
            control={control}
            data={value}
          />
        );

      case "upload":
        return (
          <Controller
            as={InputFile}
            name={value.name}
            control={control}
            data={value}
          />
        );

      case "colorPicker":
        return (
          <Controller
            as={ColorPicker}
            name={value.name}
            control={control}
            data={value}
          />
        );
      case "switch":
        return (
          <Controller
            as={Switch}
            name={value.name}
            control={control}
            data={value}
          />
        );
      case "css":
        return (
          <Controller
            as={CodeEditor}
            name={value.name}
            control={control}
            data={value}
            editorLanguage="css"
          />
        );
      default:
        return null;
    }
  };

  const renderItemWrap = (value: FieldItem) => {
    if (
      value.parentRequire &&
      Object.keys(value.parentRequire).some((item) => {
        return value.parentRequire?.[item] !== watch(item);
      })
    ) {
      return null;
    }

    return (
      <div key={value.name} className="mb3 w-100">
        <label className="ttc db mv2 fw6" htmlFor={value.name}>
          {value.label}
        </label>
        {renderItem(value)}
        {errors[value.name] && (
          <span className="red f6 db">This field is required</span>
        )}
      </div>
    );
  };

  const _renderSubmit = () => {
    if (renderSubmit) {
      return renderSubmit(isLoading);
    }
    return (
      <button
        type="submit"
        className="pv3 ph4 white bg-pink bw0 br2 pointer w-100 flex items-center justify-center fw6 tracked-mega ttu"
        disabled={isLoading}
      >
        {isLoading ? (
          <div className="loader" />
        ) : (
          window.WilokeNBGlob?.WilokeNotificationBarTranslations?.submit ||
          "S U B M I T"
        )}
      </button>
    );
  };

  return (
    <form className="wilForm" onSubmit={handleSubmit(_onSubmit)}>
      {data.map(renderItemWrap)}
      {_renderSubmit()}
    </form>
  );
};

export default MyForm;
